import { getArticleComments } from '@/api/api'
export default {
	name:'articleComponents',
	props: {
		article: {
			type: Object,
			required: true
		}
	},
	data() {
		return {
			comments: []
		}
	},
	async mounted () {
		const { data } = await getArticleComments(this.article.slug)
		this.comments = data.comments
		console.log(data,"data")
	}
};
